body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container {
  padding-right: 20px;
  display: grid;
  grid-template-columns: 70% 30%;
  text-align: center;
  margin-top: 15px;
  box-sizing: border-box;
}

.colum-right:after {
  content: "";
  position: absolute;
  bottom: 82%;
  left: 52%;
  width: 25%;
  height: 2px;
  margin-left: 20%;
  background: #000;
}
.item {
  border-right: 2px solid black;
  display: grid;
  grid-template-columns: 33% 33% 33%;
}
.item_list > div {
  margin-bottom: 20px;
}
h4 {
  margin: 0px;
}
h2 {
  margin-top: 25px;
  padding-bottom: 15px;
}
/* .item {
} */
.selectedData {
  margin: 0 auto;
  padding-top: 35px;
  box-sizing: border-box;
}

.selected {
  color: red;
}
